<template>
    <div>
        <form @submit="PaySubmit">
            <p class="error">{{ error }}</p>
            <!--<p class="decode-result">Last result: <b>{{ result }}</b></p>-->
            <qrcode-stream v-if="!result.order_no" @decode="onDecode" @init="onInit" /><br><br>

            <div v-if="result.order_no">
                <a class="list-group-item list-group-item-action flex-column align-items-start">
                    <div class="d-flex w-100 justify-content-between">
                        <h5 class="mb-1">  </h5>
                        <small class="text-muted"> : {{result.order_no}}</small>
                    </div>
                    <p class="mb-1">{{result.product}}</p>
                    <small class="text-muted">{{result.price}}</small>
                </a>
                <button @click="onResetSubmit" type="submit" class="btn btn-primary">QR </button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "Pay",
        data() {
            return {
                result : {},
                ordernumber : '',
                error : ''
            }
        },
        methods: {
            onResetSubmit : function() {
                this.result = {};
            },
            onDecode (ordernumber) {
                this.ordernumber = ordernumber
            },
            async onInit (promise) {
                try {
                    await promise
                } catch (error) {
                    if (error.name === 'NotAllowedError') {
                        this.error = "ERROR:   . "
                    } else if (error.name === 'NotFoundError') {
                        this.error = "ERROR:  . "
                    } else if (error.name === 'NotSupportedError') {
                        this.error = "ERROR:    (HTTPS, localhost)"
                    } else if (error.name === 'NotReadableError') {
                        this.error = "ERROR:   "
                    } else if (error.name === 'OverconstrainedError') {
                        this.error = "ERROR:   "
                    } else if (error.name === 'StreamApiNotSupportedError') {
                        this.error = "ERROR:     API"
                    }
                }
            },
            PaySubmit() {
                let number = '';
                let t = '';
                let exit = false;
                let i = 0;//   
                let j = 0;//0 , 1 jwt
                while(exit) {
                    if(this.ordernumber.charAt(i)) {
                        if(j==0) {
                            number = number + this.ordernumber.charAt(i);
                        }
                        else {
                            t = t + this.ordernumber.charAt(i);
                        }
                    }
                }
                const Payinfo = {
                    order_no : number,
                    t : t
                };
                alert('payinfo : '+JSON.stringify(Payinfo));
                this.$store.dispatch('PAY', Payinfo)
                    .then(response => {
                        console.log(response);
                    })
            }
        },
        watch : {
            ordernumber : function (order) {
                let ordernum = {
                    orderno : order
                };
                this.$store.dispatch('GetOrder', ordernum)
                    .then( response => {
                        //alert('  : '+JSON.stringify(response.data.order[0]));
                        this.result = response.data.order[0];
                        alert('  : '+JSON.stringify(response.data));
                    })
                    .catch( err => {
                        console.log('  : ' + err);
                        //alert(err);
                    })
            }
        }
    }
</script>

